import React from 'react'
import classnames from 'classnames'
import './index.less'
import { observer } from 'mobx-react'

const button = observer((props) => {
  let classes = classnames({'component-button': true, 'disabled': props.disabled, 'radius': props.radius})
  return (
    <div
      className={classes}
      onClick={props.onClick}
      style={props.style}
      {...props}>{props.children}</div>
  )
})

export default button
