import React, { Component } from 'react'
import Util from 'util'
import { Link } from 'react-router-dom'

import './index.less'

class ListAlbum extends Component {
  stopPropagation = (e) => {
    if (e.nativeEvent.target.name === 'stopPropagation') e.preventDefault()
  }

  render () {
    return (
      <div className='list-container'>
        <ul className='item-wrap clearfix'>
          <li>
            <Link to={this.props.albumUrl} onClick={this.stopPropagation}>
              <div className='img' >
                <img src={this.props.imgUrl} alt='' />
              </div>
              <div className='info-text'>
                <div className='left'>
                  <h2 className='title'>{this.props.title}</h2>

                  {
                    this.props.announcer
                      ? <p>by {this.props.announcer}</p>
                      : <p>{this.props.info}</p>
                  }

                  <div className='count-info' >
                    {
                      this.props.playcount
                        ? <span className='playcount'>
                          <img src={require('../../assets/images/Icons/info_ic_playtimes.png')} alt='' />
                          {Util.transPlayCount(this.props.playcount)}
                        </span> : ''
                    }
                    {
                      this.props.albumAnnouncer &&
                      <span className='playcount'>
                        {/* <img src={require('../../assets/images/Icons/info_ic_playtimes.png')} alt='' /> */}
                        {Util.transPlayCount(this.props.albumAnnouncer)}
                      </span>
                    }

                    {
                      this.props.setNum &&
                      <span className='setnum'>
                        <img src={require('../../assets/images/Icons/info_ic_track.png')} alt='' />
                        {Util.transPlayCount(this.props.setNum)}集
                      </span>
                    }
                    {
                      this.props.time &&
                      <span className='setnum'>
                        <img src={require('../../assets/images/Icons/info_ic_time.png')} alt='' />
                        {Util.formatDuration(this.props.time)}
                      </span>
                    }
                    {
                      this.props.date &&
                      <span>
                        {Util.formatDate(this.props.date)}
                      </span>
                    }
                    {
                      this.props.sowing
                        ? <span className='sowing'>
                          已播{parseInt(this.props.sowing * 100) ? parseInt(this.props.sowing * 100) : 1
                          }
                          %
                        </span>
                        : ''
                    }

                  </div>
                </div>
                {
                  this.props.del && <div className='right del' onClick={this.props.onTouch}>
                    <img name='stopPropagation' src={require('../../assets/images/Icons/ic_delete_small.png')} alt='' />
                  </div>
                }
                {
                  this.props.isLink && <div className='right'onClick={this.props.onTouch}>link</div>
                }
                {
                  this.props.isStart && <div className='right isStart' onClick={this.props.onTouch}>
                    <img name='stopPropagation' src={require('../../assets/images/Icons/ic_collect_selected.png')} alt='' />
                  </div>
                }

              </div>
            </Link>
          </li>
        </ul>
      </div>
    )
  }
}

export default ListAlbum
