import React, { Component } from 'react'
// import { Link } from 'react-router-dom'
import classnames from 'classnames'
import { observer, inject } from 'mobx-react'
import './index.less'

@inject('store')
@observer
class CardItemSmall extends Component {
  render () {
    const { player: { playState } } = this.props.store
    const playClass = classnames('playbtns', { 'current': this.props.current ? (playState === 2) : false })
    return (
      <div className='cardsmall-container'>
        <img src={this.props.bgUrl} alt='bg' />
        <span className='title'>{this.props.title}</span>
        {this.props.isShowPlay && <div className={playClass} onClick={this.props.onClick} />}

      </div>
    )
  }
}

export default CardItemSmall
