import React, { Component } from 'react'
// import Util from 'util'
import TabNav from '@/components/tabnav/tabNav'
import { observer, inject } from 'mobx-react'
import AlbumBrief from './albumBrief'
import ProgramList from './programList'
import './briefItem.less'

@inject('store')
@observer
class BriefItem extends Component {
  componentDidMount () {

  }
  render () {
    const info = this.props.albumInfo
    const {tracks} = this.props.store.album

    return (
      <div className='briefItem-container'>
        <TabNav isborder>
          <div className='item' name='简介'>
            <AlbumBrief albumInfo={info} />
          </div>
          <div className='item' name={`节目（${info.include_track_count}）`}>
            <ProgramList listitem={tracks} />
          </div>
        </TabNav>
      </div>
    )
  }
}
export default BriefItem
