import { toast } from 'react-toastify'

export function mockAsync (data) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(data)
    }, 2000)
  })
}

// 生成随机字符串，默认32位
export const generateRandom = (n = 32) => {
  const chars = '0123456789abcdefghijklmnopqrstuvwxyz'
  let res = ''
  for (let i = 0; i < n; i += 1) {
    const id = Math.ceil(Math.random() * 35)
    res += chars[id]
  }
  return res
}

// 本地化存储
export const getData = key => JSON.parse(localStorage.getItem(key))

export const setData = (key, item) => localStorage.setItem(key, JSON.stringify(item))

// 错误，正确的信息的显示
export function showError (msg) {
  toast.error(msg, {
    position: 'top-right',
    autoClose: 2000,
    hideProgressBar: false
  })
}

export function showSuccess (msg) {
  toast.success(msg, {
    position: 'top-right',
    autoClose: 2000,
    hideProgressBar: false
  })
}

export const random = (num) => {
  var text = ''
  var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'

  for (var i = 0; i < num; i++) { text += possible.charAt(Math.floor(Math.random() * possible.length)) }

  return text
}

export const getQueryString = (name) => {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  var r = window.location.search.substr(1).match(reg)
  if (r != null) {
    return unescape(r[2])
  }
  return null
}

// 日期转换
export const formatDate = (time, char = '-') => {
  const date = new Date(time)
  const y = 1900 + date.getYear()
  const m = `0${date.getMonth() + 1}`
  const d = `0${date.getDate()}`
  return `${y}${char}${m.substring(m.length - 2, m.length)}${char}${d.substring(d.length - 2, d.length)}`
}

// 时长转换
export const formatDuration = (time = 0, withHour = false) => {
  if (time === 86400) {
    return '24:00'
  }
  const seconds = parseInt(time % 60) >= 10
    ? parseInt(time % 60)
    : `0${parseInt(time % 60)}`
  const minutes = parseInt(time / 60 % 60) >= 10
    ? parseInt(time / 60 % 60)
    : `0${parseInt(time / 60 % 60)}`
  const hours = parseInt(time / 3600 % 24)
  if (hours !== 0) {
    return `${hours}:${minutes}:${seconds}`
  }
  if (withHour) {
    return `00:${minutes}:${seconds}`
  }
  return `${minutes}:${seconds}`
}

// 播放数转换
export const transPlayCount = (count) => {
  if (count > 100000000) {
    return `${(count / 100000000).toFixed(1)}亿`
  }
  if (count > 10000) {
    return `${(count / 10000).toFixed(1)}万`
  }
  return count
}

export const ModalScroll = (() => {
  var scrollTop

  return {
    afterOpen () {
      try {
        scrollTop = document.scrollingElement.scrollTop
        document.body.style.cssText = 'position:fixed;top:-' + scrollTop + 'px'
      } catch (e) {}
    },
    beforeClose: function () {
      try {
        document.body.style.cssText = 'position:initial'
        // scrollTop lost after set position:fixed, restore it back.
        document.scrollingElement.scrollTop = scrollTop
      } catch (e) {}
    }
  }
})()

// 避免重复的异步获取资源
/*
  callback: 需要回调的异步请求，可以是async function，普通方法（返回常量，promise，不返回都可以）
  timeout: 超时时间，防止promise一直处于pending状态
*/
export const getFetchingLock = (timeout) => {
  let lock = false

  const timeOutTrigger = (time) => new Promise(resolve => setTimeout(() => {
    resolve(undefined)
  }, time))

  return async (callback) => {
    try {
      if (!lock) {
        lock = true
        const result = await Promise.race([callback(), timeOutTrigger(timeout)])
        lock = false
        return result
      }
    } catch (e) {
      lock = false
      throw new Error(e)
    }
  }
}
