import React, { Component } from 'react'
// import { Link } from 'react-router-dom'
import { observer, inject } from 'mobx-react'
import CardItem from '@/components/cardList-big/cardBig'
import { homeClassify } from '@/constant'
import './homelist.less'

@inject('store')
@observer
class FlowList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      current: null
    }
  }

  onClick=async (ele, index, e) => {
    e.stopPropagation()
    // 通过点击tag 获取list 播放第一首
    let id = null
    const { player } = this.props.store
    if (ele === '付费排行') {
      await this.props.store.category.getPayRank()
      id = this.props.store.category.firstIdRank
    } else {
      await this.props.store.category.getAlbumByTag(ele)
      id = this.props.store.category.firstId
    }
    if (player.track && player.track.id === +id && player.playState === 2) {
      this.props.store.player.playAndPause()
    } else {
      this.props.store.player.play(id)
    }

    this.setState({
      isCurrent: index
    })
  }

  onGetList2=(name) => { // 点击进入列表页面
    this.props.history.push(`/classify/${name}`)
  }

  render () {
    return (
      <div className='homelist-container'>
        <ul>
          {
            homeClassify.listItem.map((ele, index) => {
              return (
                <li key={index} onClick={() => this.onGetList2(ele.tagname)}>
                  <CardItem
                    title={ele.title}
                    bgUrl={ele.bgUrl}
                    tagName={ele.tagname}
                    onClick={(e) => this.onClick(ele.tagname, index, e)}
                    isShowPlay={ele.isShowPlay}
                    current={this.state.isCurrent === index}
                    playState={this.props.store.player.playState}
                  />
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default FlowList
