import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import './index.less'

@inject('store')
@observer
class Pay extends Component {
  componentDidMount () {
    const id = this.props.match.params.id
    this.props.store.user.getUserInfo()
    this.props.store.album.getAlbumInfo(id)
  }
  // 提交订单
  onSubmitOrder= (price) => {
    const id = this.props.match.params.id
    this.props.store.order.Order(2, id, 0.1)
  }
  // 申请退款
  return=() => {
    console.log('tuiukuan ')
    this.props.store.order.onapplyReturn('2018072402010261600124047899')
  }

  render () {
    const userinfo = this.props.store.user.userInfo
    const {albumInfo} = this.props.store.album
    const {price_infos} = albumInfo
    const {cover} = albumInfo
    if (!userinfo || !albumInfo) return null
    if (!cover) return null

    return (
      <div className='order-container'>
        <div className='order-content'>
          <div className='pay-user' onClick={this.return}>
            <span>购买账号</span>
            <span>{userinfo.nickname}</span>
          </div>
          <div className='order-info'>
            <div className='content'>
              <img src={cover.middle.url} alt='' />
              <div className='text'>
                <p>
                  <span className='name'>{albumInfo.title}</span>
                  <span>X1</span>
                </p>
                <p className='bottom'>
                  <span className='tip'>7天无忧退</span>
                  {
                    price_infos
                      ? <span className='price'>{price_infos[0].discounted_price}{price_infos[0].price_unit}</span>
                      : <span className='price'>12</span>
                  }

                </p>
              </div>
            </div>
            <div className='total-price'>
              <span>总价</span>
              {
                price_infos
                  ? <span className='price-num'> {price_infos[0].discounted_price}{price_infos[0].price_unit}</span>
                  : <span className='price'>12</span>
              }

            </div>
          </div>
          <div className='bottom-btn'>
            {
              price_infos
                ? <div className='price'>应付：{price_infos[0].discounted_price}{price_infos[0].price_unit}</div>
                : <div className='price'>应付：12</div>
            }
            {/* <div className='confirm-btn' onClick={() => this.onSubmitOrder(price_infos[0].discounted_price)}>提交订单</div> */}
            <div className='confirm-btn' onClick={this.onSubmitOrder}>提交订单</div>
          </div>

        </div>
      </div>

    )
  }
}

export default Pay
