import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import { debounce } from 'lodash'
import lozad from 'lozad'
import Start from './components/startSearch'
import SearchResult from './components/searchResult'
import { Link } from 'react-router-dom'
// import { carExit } from '../../utils/car'
import './index.less'

@inject('store')
@observer
class Search extends Component {
  constructor () {
    super()
    this.lazy = lozad()
  }

  componentDidMount () {
    this.input.value = this.props.store.search.keyWord
    if (this.props.store.search.keyWord) {
      this.props.store.search.fetchData()
    }
    this.lazy.observe()
  }

  componentDidUpdate () {
    this.lazy.observe()
  }

  setKeyWord = debounce(() => {
    const value = this.input.value
    this.props.store.search.setKeyWord(value)
  }, 1000)

  searchSuggest = () => {
    if (this.input.value) {
      this.props.store.search.searchSuggest(this.input.value)
    }
  }

  searchWord = (word) => {
    this.input.value = word
    this.props.store.search.setKeyWord(word)
  }

  keyPress = (e) => {
    if (e.nativeEvent.charCode === 13) {
      this.search()
    }
  }

  search = () => {
    this.props.store.search.setKeyWord(this.input.value)
  }

  cancel = () => {
    if (this.props.store.search.keyWord) {
      this.props.store.search.setKeyWord('')
      this.input.value = ''
    } else {
      this.props.history.goBack()
    }
  }

  // goBack = () => {
  //   if (this.input.value) {
  //     this.input.value = ''
  //     this.search()
  //     return
  //   }
  //   if (this.props.history.location.pathname === '/') {
  //     // carExit()
  //   } else {
  //     this.props.history.goBack()
  //   }
  // }

  render () {
    const { keyWord, showSuggestList, searchSuggestList, keywordsList } = this.props.store.search
    if (!this.props.store.search) return null
    let htmls = ''
    if (!keyWord) {
      if (showSuggestList) {
        htmls = (
          <div>
            <ul className='suggesrAlbum-wrap'>
              {
                searchSuggestList.map((item, index) => {
                  return (
                    <li className='item' key={index}>
                      <Link to={`/album/${item.id}`}>
                        <div className='img'><img src={item.cover_url_small} alt='专辑封面' /></div>
                        <div className='text-info'>
                          <div className='left'>
                            <h2>{item.album_title}</h2>
                            <p>{item.category_name}</p>
                          </div>
                          <div className='right'>
                            <img src={require('../../assets/images/Buttons/card_btn_play_small.png')} alt='' />
                          </div>

                        </div>
                      </Link>
                    </li>
                  )
                })
              }
            </ul>
            <ul className='highlightKeyword-wrap'>
              {
                keywordsList.map((item, index) => {
                  return (
                    <li key={index} onClick={() => this.searchWord(item.keyword)}>
                      <p dangerouslySetInnerHTML={{ __html: item.highlight_keyword }} />
                    </li>
                  )
                })
              }
            </ul>
          </div>
        )
      } else {
        htmls = (
          <div className='searchStart-block' style={{ display: keyWord ? 'none' : 'block' }}>
            <Start searchWord={this.searchWord} />
          </div>
        )
      }
    }

    return (
      <div className='search-page container'>
        <div className='searchHeader'>
          {
            keyWord
              ? <input
                onKeyPress={this.keyPress}
                ref={(input) => { this.input = input }}
                placeholder='搜索专辑、声音、主播' type='text' />
              : <input
                onKeyPress={this.keyPress}
                ref={(input) => { this.input = input }}
                onChange={this.searchSuggest}
                placeholder='搜索专辑、声音、主播' type='text' />
          }
        </div>

        <div className='search-main' >
          {htmls}
          {/* {
            !keyWord && <div className='searchStart-block' style={{ display: keyWord ? 'none' : 'block' }}>
              <Start searchWord={this.searchWord} />
            </div>
          } */}

          {
            keyWord &&
            <div>
              <SearchResult resultdata={this.props.store.search} {...this.props} />
            </div>
          }
        </div>

      </div >
    )
  }
}

export default Search
