import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import UserInfo from './components/userinfo'
// import UserItemList from './components/userItemList'
import OperList from '@/components/operLlist/operlist'
import Button from '@/components/button/button'
import './index.less'
import { logOut } from '@/request'

@inject('store')
@observer
class User extends Component {
  componentDidMount () {
    this.props.store.user.getUserInfo()
    this.props.store.bought.boughtList()
  }

  render () {
    if (!this.props.store.bought) return null
    const {boughdata} = this.props.store.bought

    return (
      <div className='user-page'>
        <UserInfo userinfo={this.props.store.user.userInfo} />

        {/* <div className='list' num='400'>喜点</div> */}
        <div className='list-container' >已购 {boughdata.length}</div>

        <OperList islink listUrl='/' title='下载喜马拉雅APP' />
        <OperList islink listUrl='/about' title='关于我们' />
        <Button onClick={logOut} style={{backgroundColor: '#fff', color: '#FF542C'}}>退出登录</Button>
      </div>
    )
  }
}
export default User
