import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import { withRouter } from 'react-router-dom'
import Popup from '@/components/popup/popup'
import PlayerController from './components/playerController'
import PlayList from './components/playerList'
import './index.less'

@withRouter
@inject('store')
@observer
export default class PlayerTrack extends Component {
  componentWillMount () {
    const { match, store } = this.props
    const { id } = match.params
    if (id) {
      store.player.play(id)
    }
  }
  onClose=() => {
    this.props.store.player.showTrackList()
  }
  render () {
    const { player } = this.props.store
    const { isShowTrackList } = player
    const { announcer, image, title } = player.track
    const nickname = announcer ? announcer.nickname : ''
    return (
      <div className='player-page'>
        <div className='player-album'>
          <div className='player-album-img'>
            <img src={image ? image.url : ''} className='full-img' />
          </div>
          <p className='album-title'>{title}</p>
          <p className='album-nickname'>{nickname}</p>
        </div>

        <PlayerController />

        {
          isShowTrackList &&
          <Popup onShow={isShowTrackList} onClose={this.onClose}>
            <PlayList />
          </Popup>
        }

      </div>
    )
  }
}
