/*
  Route -> props -> Components
  invalid params for component: path, component, exact
  valid params for component: routes, basicRouteInfo, ...rest
*/
import { Route } from 'react-router-dom'
import React, { Component } from 'react'
import { inject, observer } from 'mobx-react'
import { getData } from '@/utils'
import { OAuth2Login } from '@/request'

@inject('store')
@observer
export default class BaseRoute extends Component {
  componentDidMount () {
    const { title, store } = this.props
    store.router.changeTitle(title)
  }

  render () {
    const { path, component: Component, exact, title, store, logic, auth = true, ...rest } = this.props
    return (
      <Route
        path={path}
        exact={exact}
        render={(props) => {
          if (logic) logic(props)
          if (auth) {
            if (!store.user.accessToken) {
              const token = getData('token')
              if (token) {
                store.user.setAccessToken(token.access_token)
              } else {
                return OAuth2Login()
              }
            }
          }
          return (<Component {...rest} {...props} />)
        }}
      />
    )
  }
}
