import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import ScrollBar from '@/components/scrollBar/scrollBar'
import './playerList.less'

@inject('store')
@observer
class PlayList extends Component {
  componentDidUpdate () {
    const items = document.querySelectorAll('.playerList-item')
    const activeItem = document.querySelector('.playerList-items .active')
    const trackIndex = Array.from(items).indexOf(activeItem)
    if (trackIndex && document.querySelector('.playerList-items').scrollTop === 0 && document.querySelector('.playerList-item')) {
      const scrollTop = trackIndex * document.querySelector('.playerList-item').offsetHeight
      document.querySelector('.playerList-items').scrollTop = scrollTop
    }
  }

  render () {
    const { player } = this.props.store
    const {
      playMode,
      switchPlayMode,
      isAsc,
      switchSort,
      track,
      trackList,
      showTrackList,
      playState,
      fetchNext

    } = player

    return (
      <div className='playerList-container'>
        <div onClick={showTrackList} className='playerList-mask' />
        <ScrollBar
          style={{height: '100%'}}
          onReachBottom={fetchNext}
        >
          <div className='playerList-bar'>
            <div className='playerList-buttons'>
              <button onClick={switchPlayMode} className='leftBtn'>
                {playMode === 0 ? <span className='shunxubofang'><img src={require('../../../assets/images/Icons/ic_lbxh.png')} alt='' /> 循环播放</span> : null}
                {playMode === 1 ? <span className='danquxunhuan'> <img src={require('../../../assets/images/Icons/ic_dqxh.png')} alt='' /> 单曲循环</span> : null}
                {playMode === 2 ? <span className='suijibofang'> <img src={require('../../../assets/images/Icons/ic_sjbf.png')} alt='' /> 随机播放</span> : null}
              </button>
              <button onClick={switchSort} className='rightBtn'>
                {isAsc
                  ? <span> <img src={require('../../../assets/images/Icons/ic_sxbf.png')} alt='' /> 顺序 </span>
                  : <span> <img src={require('../../../assets/images/Icons/ic_dx.png')} alt='' /> 倒序 </span>
                }
              </button>
            </div>
            <ul className='playerList-items'>
              {trackList.map((item, index) =>
                (<li
                  className={`playerList-item ${+track.id === +item.id ? 'active' : ''}`}
                  key={item.id}
                >
                  {/* <span className='playerList-index'>{index + 1}</span> */}
                  <span className='playerList-name' onClick={() => player.play(item.id)}> {item.title}</span>

                  <div className={`${playState === 2 ? ' playing-icon' : 'pause playing-icon'}`}>
                    <span />
                    <span />
                    <span />
                    <span />
                  </div>
                </li>)
              )}
            </ul>
          </div>
        </ScrollBar>
      </div>
    )
  }
}

export default PlayList
