import React, { Component } from 'react'
// import Util from 'util'
import './albumPrice.less'

class AlbumPrice extends Component {
  // 免费试听
  freePlay=(id) => {
    this.props.history.push(`/player/${id}`)
  }
  render () {
    const info = this.props.albumInfo
    const {tracksList} = this.props.store.album
    const {price_infos} = info
    if (!info) return null
    console.log(info)

    // 没有免费声音的 专辑，查看专辑里面第一个声音看是否是石头声音

    let freehtml = ''
    if (price_infos[0].free_track_ids.split(',')[0]) {
      freehtml = (
        <button className='free-button' onClick={() => this.freePlay(price_infos[0].free_track_ids.split(',')[0])}>免费试听</button>
      )
    } else {
      if (tracksList && tracksList[0].has_sample) {
        freehtml = (
          <button className='free-button' onClick={() => this.freePlay(tracksList[0].id)}>免费试听</button>
        )
      }
    }
    return (
      <div className='albumPrice-container'>
        <div className='albumPrice-content'>
          {/* {
            price_infos[0].free_track_ids.split(',')[0] &&
            <button className='free-button' onClick={() => this.freePlay(price_infos[0].free_track_ids.split(',')[0])}>免费试听</button>
          } */}
          {freehtml}

          <button className='pay-button'
            onClick={this.props.onClick}>购买
            {
              price_infos &&
              <span>{price_infos[0].discounted_price}{price_infos[0].price_unit}</span>
            }
          </button>
        </div>
      </div>
    )
  }
}
export default AlbumPrice
