import React, { Component } from 'react'
import Util from 'util'
import { observer, inject } from 'mobx-react'
import Button from '@/components/button/button'
import { withRouter } from 'react-router'
import './albumCard-2.less'

@withRouter
@inject('store')
@observer
class AlbumCard extends Component {
  playAll = () => {
    const firstTrack = this.props.store.album.tracksList[0]
    if (!firstTrack) return
    this.props.history.push(`/player/${firstTrack.id}`)
  }

  render () {
    const info = this.props.albumInfo
    const {cover} = info
    if (!cover) return null

    return (
      <div className='albumCards-container'>
        <div className='albumCards-content'>
          <div className='albumcover'>
            <img src={info.cover.large.url} alt='' />
            <div className='playcount'><span>{Util.transPlayCount(info.play_count)}</span></div>
          </div>
          <div className='text-info'>
            <p className='album-name'>{info.title}</p>
            <p className='albumInfo-data--nikename'>
              {info.announcer && <img src={info.announcer.avatar_url} alt='' />}
              {info.announcer && <span>{info.announcer.nickname}</span> }
            </p>
            <p className='update-time'>{Util.formatDate(info.updated_at)}更新</p>
          </div>
        </div>
        <div className='albumCards-button'>
          {
            this.props.store.album.issubscribed
              ? <Button style={{backgroundColor: '#E8EBEF', color: '#909FB0', width: '1.65rem'}} radius='4' disabled onClick={this.props.onClick}>已订阅</Button>
              : <Button style={{backgroundColor: '#E7F2FB', color: '#107FE0', width: '1.65rem'}} radius='4' onClick={this.props.onClick}>订阅</Button>
          }
          <Button onClick={this.playAll} style={{backgroundColor: '#107FE0', color: '#fff', width: '1.65rem'}} radius='4'>播放全部</Button>
        </div>

      </div>
    )
  }
}
export default AlbumCard
