import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import TabLink from '@/components/tabLink/tabLink'
// import styled from 'styled-components'
import FlowList from '@/components/flowList/flowList'
import HomeList from './components/homeList'
import CurrentPlay from './components/currenPlay'
import './index.less'

@inject('store')
@observer
export default class Home extends Component {
  render () {
    const { track } = this.props.store.player
    const hasTrack = !!Object.keys(track).length
    return (
      <div className='home-page'>
        <TabLink >
          <li title='搜索' link='/search'>
            <img src={require('../../assets/images/Icons/ic_search.png')} alt='bg' />
          </li>
          <li title='首页' link='/'key='home'>首页</li>
          <li title='分类'link='/classify' >分类</li>
          {/* <li title='广播' link='/broadcast'>广播</li> */}
          <li title='个人中心' link='/user'>
            <img src={require('../../assets/images/Icons/ic_user.png')} alt='bg' />
          </li>
        </TabLink>
        <FlowList>
          <li title='订阅' url='/subscribeAlbum' size='32'>  <img src={require('../../assets/images/Icons/home_ic_collection.png')} alt='bg' /></li>
          <li title='历史' url='/history' size='32'> <img src={require('../../assets/images/Icons/home_ic_history.png')} alt='bg' /></li>
          <li title='已购' url='/boughtList' size='32'> <img src={require('../../assets/images/Icons/home_ic_purchase.png')} alt='bg' /></li>
        </FlowList>

        <HomeList {...this.props} />

        {/* 这是logo */}
        <div className='logo'>
          <img src={require('../../assets/images/Images/img_logo.png')} alt='bg' />
        </div>
        { hasTrack && <CurrentPlay /> }
      </div>
    )
  }
}
