import React, { Component } from 'react'
// import { Link } from 'react-router-dom'
import Button from '@/components/button/button'
import {classifyText} from '@/constant'
import './classifyButton.less'

class ClassifyButton extends Component {
  getTagList=(name) => {
    this.props.history.push(`/classify/${name}`)
  }
  render () {
    if (!this.props.classifyList) return null
    return (
      <div className='classifyButton-container'>
        <div className='classify-title'>知识类</div>
        <div className='content'>
          {
            classifyText.konwlist.map((ele, index) => {
              return (
                <div key={index} className='item' tagname={ele.tagname} onClick={() => this.getTagList(ele.tagname)}>
                  <Button style={{width: '1.1rem', color: ' #59708A'}} radius='4px'>{ele.title}</Button>
                </div>
              )
            })
          }
        </div>

        <div className='classify-title'>娱乐类</div>
        <div className='content'>
          {
            classifyText.amuselist.map((ele, index) => {
              return (
                <div key={index} className='item' tagname={ele.tagname} onClick={() => this.getTagList(ele.tagname)}>
                  <Button style={{width: '1.1rem', color: ' #59708A'}} radius='4px'>{ele.title}</Button>
                </div>
              )
            })
          }
        </div>
        <div className='classify-title'>生活类</div>
        <div className='content'>
          {
            classifyText.lifelist.map((ele, index) => {
              return (
                <div key={index} className='item' tagname={ele.tagname} onClick={() => this.getTagList(ele.tagname)}>
                  <Button style={{width: '1.1rem', color: ' #59708A'}} radius='4px'>{ele.title}</Button>
                </div>
              )
            })
          }
        </div>

      </div>
    )
  }
}
export default ClassifyButton
