import React, { Component } from 'react'
import ListAlbum from '@/components/itemList/list'
import { observer, inject } from 'mobx-react'

@inject('store')
@observer
class HistoryList extends Component {
  componentDidMount () {
    this.props.store.history.HistoryList()
  }
  delHistory=(type, id, track_id, time) => {
    this.props.store.history.DelhistoryItem(type, id, track_id, time)
  }
  render () {
    // const historyList = this.props.store.history.historyList
    const historyList = this.props.store.history.historyList

    return (
      <div className='history-page'>
        {
          historyList.map((item, index) => {
            return (
              <div key={index}>
                {
                  // 点播 云历史
                  item.content_type === 1 &&
                  <ListAlbum
                    albumUrl={`/player/${item.track_id}`}
                    title={item.album_title}
                    imgUrl={item.album_cover_url_middle}
                    info={item.track_title}
                    sowing={(item.break_second) / (item.track_duration)}
                    id={item.album_id}
                    time={item.track_duration}
                    isPaid={item.is_paid}
                    del
                    onTouch={() => this.delHistory(item.content_type, item.album_id, item.track_id, Date.parse(new Date()))}

                  />
                }
                {
                  // 广播云历史

                }

              </div>
            )
          })

        }

      </div>
    )
  }
}

export default HistoryList
