import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import TabNav from '@/components/tabnav/tabNav'
import ResultList from './searchResultList'
import './searchResult.less'

@inject('store')
@observer
class SearchResult extends Component {
  render () {
    if (!this.props.resultdata) return null
    const resultdata = this.props.resultdata
    const {albums, tracks, radios} = resultdata
    // console.log(this.props.store.search.albums.list.length)
    return (
      <div className='searchResult-container'>
        <TabNav>
          <div className='item' name='专辑'>
            {
              albums.list.length
                ? <ResultList albums={albums} show='albums' />
                : <div className='null'>
                  <img src={require('../../../assets/images/Images/img_null_search.png')} alt='bg' />
                  <p>无搜索结果</p>
                </div>
            }

          </div>
          <div className='item' name='声音' >
            {
              tracks.list.length
                ? <ResultList tracks={tracks} show='tracks' />
                : <div className='null'>
                  <img src={require('../../../assets/images/Images/img_null_search.png')} alt='bg' />
                  <p>无搜索结果</p>
                </div>
            }

          </div>
          <div className='item' name='主播'>
            {
              radios.list.length
                ? <ResultList radios={radios} show='radios' />
                : <div className='null'>
                  <img src={require('../../../assets/images/Images/img_null_search.png')} alt='bg' />
                  <p>无搜索结果</p>
                </div>
            }
            {/* <ResultList radios={radios} show='radios' /> */}
          </div>

        </TabNav>
      </div>

    )
  }
}

export default SearchResult
