import React, { Component } from 'react'
// import Util from 'util'
import './textinfo.less'

class AlbumBrief extends Component {
  render () {
    const info = this.props.albumInfo
    let htmls = ''
    if (info.rich_intro) {
      htmls = (
        <p dangerouslySetInnerHTML={{ __html: info.rich_intro }} />
      )
    }
    if (!info.rich_intro) {
      if (!info.intro) {
        htmls = (
          <p dangerouslySetInnerHTML={{ __html: info.short_intro }} />
        )
      } else {
        htmls = (
          <p dangerouslySetInnerHTML={{ __html: info.intro }} />
        )
      }
    }

    if (!info.intro && !info.rich_intro && !info.short_intro) {
      htmls = (
        <p>暂无简介</p>
      )
    }
    return (
      <div className='albumBrief-container textinfo-container'>
        <div className='albumBrief-content'>
          <h2>简介</h2>
          {htmls}
        </div>
        {/* <div className='showmore'>展开全部</div> */}
      </div>
    )
  }
}
export default AlbumBrief
