import React, { Component } from 'react'
import './playerController.less'
import { Images } from '@/constant'
import { formatDuration } from 'util'
import { observer, inject } from 'mobx-react'

@inject('store')
@observer
class playerController extends Component {
  setProgress = (e) => {
    e.preventDefault()
    const {player} = this.props.store
    const width = document.getElementsByClassName('player-progressBar')[0].clientWidth
    const x = e.nativeEvent.offsetX
    const time = x / width * player.track.duration
    player.seek(time)
  }

  handleMove = (e) => {
    const {player} = this.props.store
    const offsetLeft = document.getElementsByClassName('player-progressBar')[0].getBoundingClientRect().left
    const maxWidth = document.getElementsByClassName('player-progressBar')[0].clientWidth
    const minWidth = 0
    let x = e.touches[0].pageX - offsetLeft
    if (x > maxWidth) {
      x = maxWidth
    } else if (x < minWidth) {
      x = minWidth
    }
    const percent = ((x / maxWidth) * 100) + '%'
    document.getElementsByClassName('player-progressBar-passed')[0].style.width = percent
    document.getElementsByClassName('player-progressBar-movehandler')[0].style.left = percent
    const time = x / maxWidth * player.track.duration
    player.seek(time)
  }
  onSubscribe=() => {
    const id = this.props.match.params.id
    if (this.props.store.album.issubscribed) {
      this.props.store.album.subscribeOper(0, id)
    } else {
      this.props.store.album.subscribeOper(1, id)
    }
  }

  render () {
    const {player} = this.props.store
    const { position, percentage, track, showTrackList, issubscribed, onSubscribe } = player
    const { duration } = track
    return (
      <div className='playerController-container'>
        <div className='player-progress'>
          <div className='player-progressBar' onClick={this.setProgress}>
            <div className='player-progressBar-passed' style={{ width: percentage }} />
            <div className='player-progressBar-movehandler' style={{ left: percentage }} onTouchMove={this.handleMove} />
          </div>
          <div className='txt-wrapper'>
            <span className='progress-txt'>{formatDuration(position)}</span>
            <span className='progress-txt'>{formatDuration(duration)}</span>
          </div>
        </div>
        <div className='button-controller'>
          <div onClick={showTrackList} className='ps-button flex column center'>
            <div style={{width: 24}}>
              <img src={Images.ic_playlist}className='full-img' />
            </div>
            <span className='small-txt'>播放列表</span>
          </div>

          <div onClick={player.prev} className='np-button'>
            <img src={Images.playing_btn_previous}className='full-img' />
          </div>
          <div onClick={player.playAndPause} className='play-button'>
            <img src={player.playState === 2 ? Images.playing_btn_pause : Images.playing_btn_play} className='full-img' />
          </div>
          <div onClick={player.next} className='np-button'>
            <img src={Images.playing_btn_next} className='full-img' />
          </div>
          <div onClick={onSubscribe} className='ps-button flex column center'>
            {
              issubscribed
                ? <div style={{width: 24}}>
                  <img src={Images.ic_collect_selected}className='full-img' />
                </div>
                : <div style={{width: 24}}>
                  <img src={Images.ic_collect_default}className='full-img' />
                </div>
            }

            <span className='small-txt'>订阅广播</span>
          </div>
        </div>

      </div>
    )
  }
}
export default playerController
