import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import Popup from '@/components/popup/popup'
import PlayList from '@/pages/player/components/playerList'
import { withRouter } from 'react-router'
import './currentPlay.less'

@withRouter
@inject('store')
@observer
class CurrentPlay extends Component {
  onClose=() => {
    this.props.store.player.showTrackList()
  }

  goPlayer = () => {
    const { player } = this.props.store
    const { push } = this.props.history
    push(`player/${player.track.id}`)
  }

  render () {
    const { player } = this.props.store
    const { track, isShowTrackList, showTrackList, playState, playAndPause } = player
    const { nickname, image, title } = track
    return (
      <div className='currentPlay-container'>
        <div className='left' onClick={this.goPlayer}>
          <div className='img'>
            <img src={image ? image.url : ''} className='full-img' />
          </div>
          <div className='text'>
            <div className='name'>{title}</div>
            <div className='info'>{nickname}</div>
          </div>
        </div>
        <div className='right'>
          <div className='playbtn' onClick={playAndPause}>
            <img src={playState === 2 ? require('@/assets/images/Buttons/card_btn_pause_small.png') : require('@/assets/images/Buttons/card_btn_play_small.png')} />
          </div>
          <div className='menu' onClick={showTrackList}> <img src={require('@/assets/images/Icons/ic_playlist.png')} /></div>
        </div>
        {
          isShowTrackList &&
          <Popup onShow={isShowTrackList} onClose={this.onClose}>
            <PlayList />
          </Popup>
        }
      </div>
    )
  }
}
export default CurrentPlay
