import { observable, action, runInAction } from 'mobx'

import historyList from '@/request/api/historyList'
import delhistoryItem from '@/request/api/delHistory'
import UpdteHistory from '@/request/api/updateHistory'
import UphistoryById from '@/request/api/uphistoryById'

class HistoryList {
  @observable historyList=[]

  @action
  async DelhistoryItem (content_type, album_id, track_id, deleted_at) {
    await delhistoryItem([{content_type, album_id, track_id, deleted_at}]).then(() => {
      runInAction('delHistory', () => {
        this.HistoryList()
      })
    })
  }

  // 上传历史
   @action
  async UphistoryItem (content_type, album_id, track_id, break_second, play_begin_at, play_end_at) {
    await UpdteHistory([{content_type, album_id, track_id, break_second, play_begin_at, play_end_at}]).then(() => {
      console.log('上传成功')
    }).catch(() => {
      console.log('上传历史失败')
    })
  }

  @action
   async UphistoryById (track_id, duration, played_secs, started_at, play_type = 0) {
     await UphistoryById([{track_id, duration, played_secs, started_at, play_type}]).then(() => {
       console.log('上传成功')
     }).catch(() => {
       console.log('上传历史失败')
     })
   }

  @action
  async HistoryList () {
    const info = await historyList().then(res => res)
    runInAction('HistoryList', () => {
      this.historyList = info.play_histories
    })
  }
}
export default HistoryList
