import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import classnames from 'classnames'
import { observer } from 'mobx-react'
import { observable, action } from 'mobx'

import './index.less'

/**
 * 使用方法
 * import Toast from '@/components/Toast';
 *
 * Toast(content, type, num)
 *
 * content：string，必须，可传string格式的HTML字符串
 * type：string，可选，目前仅'success','error','hint'三种，不传则不显示icon
 * num：number，可选，持续显示时长，默认值2000
 */
let flag = false
function toast () {
  if (flag) return
  flag = true
  let content = arguments[0]
  let duration = typeof arguments[1] === 'number' ? arguments[1] : (typeof arguments[2] === 'number' ? arguments[2] : 2000)
  let iconName = typeof arguments[1] === 'string' ? arguments[1] : (typeof arguments[2] === 'string' ? arguments[2] : '')
  let div, node // 定义toast容器变量，toast 本身

  // 不存在toast文案时，不执行
  if (!content) {
    return
  }

  // toast的状态
  class ToastData {
        @observable show = false;
        @observable content = content;
        @observable duration = duration;
        @observable iconName = iconName;
        @action.bound changeShow () {
          this.show = !this.show
        }
  }

    // toast组件定义
    @observer class Toast extends Component {
      render () {
        let toastClass = classnames({'show': this.props.toastData.show, 'toast-box': true, 'no-icon': !this.props.toastData.iconName})
        return (
          <div className='comp-toast-container'>
            <div className={toastClass} onTransitionEnd={this.disposeSelf}>
              {/* {this.props.toastData.iconName && this.props.toastData.iconName === 'success' && <img src={require('../../images/toast_success.png')} />}
              {this.props.toastData.iconName && this.props.toastData.iconName === 'error' && <img src={require('../../images/toast_error.png')} />}
              {this.props.toastData.iconName && this.props.toastData.iconName === 'hint' && <img src={require('../../images/toast_hint.png')} />} */}
              <p dangerouslySetInnerHTML={{ __html: this.props.toastData.content }} />
            </div>
          </div>
        )
      }
      componentDidMount () {
        setTimeout(() => {
          this.props.toastData.changeShow()
        }, 0)
      }
        disposeSelf = (e) => {
          if (!this.props.toastData.show) {
            this.props.disposeToast(e)
          } else {
            setTimeout(() => {
              this.props.toastData.changeShow()
            }, this.props.toastData.duration)
          }
        }
    }

    // 释放组件
    let disposeToast = (e) => {
      ReactDOM.unmountComponentAtNode(div)

      if (div.parentNode) {
        div.parentNode.removeChild(div)
        flag = false
      }
    }

    // 插入toast提示的容器至body
    div = document.createElement('div')
    document.body.appendChild(div)

    // toast
    node = (
      <Toast toastData={new ToastData()} disposeToast={disposeToast} />
    )

    // 渲染toast
    ReactDOM.render(node, div)
}

module.exports = toast
