import { observable, action, runInAction } from 'mobx'

import {placeOrder, getPaylink, applyReturn, submitOrder, getPaylinkOld} from '../request/api/order'

import {SubmitOrderPage} from '../request/index'

import Toast from '@/components/toast'

class Order {
  @observable orderinfo=[]
  @observable orderNo=null
  @observable payUrl=null
  @observable isReturn=null

  // 获取订单页面
  @action
  async getSubmitOrderPage (price_type, pay_content, price, return_url) {
    console.log(555)
    SubmitOrderPage(price_type, pay_content, price, return_url)
    // await getSubmitOrderPage2(price_type, pay_content, price, return_url)
  }

  // 提交订单

  @action
  async Order (price_type, pay_content, price) {
    await placeOrder({price_type, pay_content, price}).then((data) => {
      const {xima_order_no} = data
      runInAction('orderinfo', () => {
        this.orderNo = xima_order_no
      })

      this.onGetPayLink(this.orderNo)
    }).catch((data) => {
      if (data.response.data.error_no === 615) {
        Toast('已经确单不能再下单')
      }
      if (data.response.data.error_no === 616) {
        Toast('请勿重复购买')
      }
      if (data.response.data.error_no === 600) {
        Toast('待购买内容已下架或不存在')
      }
    })
  }

  // 根据orderno 调取支付链接

  @action
  async onGetPayLink (xima_order_no) {
    const info = await getPaylink({xima_order_no})
    const {order_settlement_url} = info
    runInAction('getPaylink', () => {
      this.payUrl = info
    })

    window.location.href = order_settlement_url
  }

  // 根据orderno 申请退款
  @action
  async onapplyReturn (xima_order_no) {
    const res = await applyReturn({xima_order_no})
    runInAction('applyreturn', () => {
      this.isReturn = res
    })
  }

  /** ****************目前走老接口 支付***********************/

  @action
  async submitorder (price_type, pay_content, price) {
    const info = await submitOrder({price_type, pay_content, price}).then((data) => {
      const {xima_order_no} = data
      console.log(data)
      runInAction('submit', () => {
        this.orderinfo = info
        this.orderNo = xima_order_no
        this.getPaylinkold(xima_order_no, window.location.origin + `/album/${pay_content}`)
      })
    }).catch((data) => {
      if (data.response.data.error_no === 615) {
        Toast('已经确单不能再下单')
      }
      if (data.response.data.error_no === 616) {
        Toast('请勿重复购买')
      }
      if (data.response.data.error_no === 600) {
        Toast('待购买内容已下架或不存在')
      }
    })
  }

  // 获取支付链接
  @action
  async getPaylinkold (xima_order_no, return_url) {
    try {
      await getPaylinkOld({xima_order_no, return_url}).then((data) => {
        const {order_settlement_url} = data
        runInAction('getlinkold', () => {
          this.payUrl = data
        })
        window.location.href = order_settlement_url
      }).catch((e) => {
        console.log(e)
      })
    } catch (e) {
      console.log(e)
    }
  }
}
export default Order
