
import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import TabLink from '@/components/tabLink/tabLink'
// import FlowList from '@/components/flowList/flowList'
import ClassifCardItem from './components/classifCard'
import ClassifyButton from './components/classifyButton'

import './index.less'

@inject('store')
@observer
export default class Classify extends Component {
  render () {
    if (!this.props.store.category.taglist) return null

    return (
      <div className='home-classify-page'>
        <TabLink >
          <li title='搜索' link='/'>
            <img src={require('../../assets/images/Icons/ic_search.png')} alt='bg' />
          </li>
          <li title='首页' link='/'key='home'>首页</li>
          <li title='分类'link='/classify' >分类</li>
          <li title='个人中心' link='/user'>
            <img src={require('../../assets/images/Icons/ic_user.png')} alt='bg' />
          </li>
        </TabLink>

        <ClassifCardItem {...this.props} />
        <ClassifyButton classifyList={this.props.store.category.taglist} {...this.props} />

      </div>
    )
  }
}
