import React, { Component } from 'react'
import Util from 'util'
import { Link } from 'react-router-dom'
import { observer, inject } from 'mobx-react'

import './programList.less'

@inject('store')
@observer
class ProgramList extends Component {
  render () {
    const {tracksList} = this.props.store.album
    return (
      <div className='programList-container'>
        <ul>
          {tracksList.length && tracksList.map((item, index) => {
            return (
              <li className='list' key={index}>
                <Link to={`/player/${item.id}`}>
                  <div className='ordernum'>{item.order_num + 1}</div>
                  <div className='contentinfo'>
                    <div className='mid'>
                      <p className='name'>{item.track_title}</p>
                      <p className='num'>
                        <span className='playcount'>{Util.transPlayCount(item.play_count)}</span>
                        <span className='setnum' >{Util.formatDuration(item.duration)}</span>
                        {
                          item.is_paid
                            ? (item.is_free && <span className='free'>免费</span>)
                            : <span className='free'>免费</span>
                        }
                      </p>
                    </div>
                    <div className='time'>{Util.formatDate(item.updated_at)}</div>
                  </div>
                </Link>
              </li>
            )
          })}

        </ul>

      </div>
    )
  }
}
export default ProgramList
