import React, { Component } from 'react'
import Util from 'util'
import './albumCard.less'

class AlbumCard extends Component {
  render () {
    const info = this.props.albumInfo
    if (!info) return null
    return (
      <div className='albumCard-container'>
        <div className='albumCard-content'>
          <div className='albumcover'>
            <img src={info.cover.large.url} alt='' />
            <div className='playcount'><span>{Util.transPlayCount(info.play_count)}</span></div>
          </div>
          <p className='album-name'>{info.title}</p>
          <p className='text'>{info.description}</p>
        </div>
        <div className='tip-list'>
          <ul>
            <li>7天无忧退</li>
            <li>会员9.5折</li>
          </ul>
        </div>
      </div>
    )
  }
}
export default AlbumCard
