import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import AlbumCard from './components/albumCard'
import AlbumCard2 from './components/albumCard-2'
import AnnouncerInfo from './components/announcer'
import AlbumBrief from './components/albumBrief'
import BuyNotes from './components/buyNotes'
import OtherNotes from './components/otherNotes'
import BriefItem from './components/briefItem'
import AlbumPrice from './components/albumPrice'
import './index.less'

@inject('store')
@observer
class AlbumInfo extends Component {
  componentWillMount () {
    const id = this.props.match.params.id
    this.props.store.album.init(id)
  }

  // 跳转下单页面
  goPay=(albumInfo) => {
    console.log(albumInfo)
    const returnrul = window.location.href
    this.props.store.order.getSubmitOrderPage(albumInfo.price_type_id, albumInfo.id, albumInfo.price_infos[0].discounted_price, returnrul)
    // window.location.href = 'http://api.test.ximalaya.com/openapi-smart-device-gateway/openapi-smart-device-pay-api/pay/goto_prepare_order?app_key=b6de6757a6b95ff388e48816e5949c0c&device_id=development&client_os_type=1&access_token=2996e0e30865a7217c054312eddb4ed5&nonce=l42e4rtnavzyukkrzq4jkils7mfh45tp&timestamp=1532937073264&price_type=2&pay_content=100066&price=0.10&return_url=album%2F100066&sig=8f2cbd9a15577b0953285b051b0ee3e8'
  }

  // 订阅专辑
  onSubscribe=() => {
    const id = this.props.match.params.id
    if (this.props.store.album.issubscribed) {
      this.props.store.album.subscribeOper(0, id)
    } else {
      this.props.store.album.subscribeOper(1, id)
    }
  }
  render () {
    const {albumInfo, issubscribed, onSubscribe} = this.props.store.album

    // 头部专辑 信息
    let topalbum = ''
    if (!albumInfo.is_paid) {
      // 免费专辑
      topalbum = (<AlbumCard2 albumInfo={albumInfo} issubscribed={issubscribed} onClick={onSubscribe} />)
    } else {
      // 付费专辑
      if (albumInfo.price_type_id === 2) {
      // 整张购买
        if (albumInfo.is_authorized) {
        // 买过
          topalbum = (
            <AlbumCard2 albumInfo={albumInfo} issubscribed={issubscribed} onClick={onSubscribe} />
          )
        } else {
          topalbum = (
            <AlbumCard albumInfo={albumInfo} />
          )
        }
      } else if (albumInfo.price_type_id === 1) {
        topalbum = (
          <AlbumCard2 albumInfo={albumInfo} issubscribed={issubscribed} onClick={onSubscribe} />
        )
      }
    }

    // 主讲人
    let speaker = ''
    if (albumInfo.is_paid && albumInfo.price_type_id === 2 && !albumInfo.is_authorized) {
      speaker = (
        <AnnouncerInfo albumInfo={albumInfo} />
      )
    }

    // 简介和节目
    let briefItem = ''
    // 付费项目 专辑卖 显示
    if (albumInfo.is_paid && albumInfo.price_type_id === 2) {
      if (albumInfo.is_authorized) {
        briefItem = (
          <BriefItem albumInfo={albumInfo} />
        )
      } else {
        briefItem = (
          <AlbumBrief albumInfo={albumInfo} />
        )
      }
    }

    // 付费项目 按集卖
    if (albumInfo.is_paid && albumInfo.price_type_id === 1) {
      briefItem = (
        <BriefItem albumInfo={albumInfo} />
      )
    }

    // 免费项目
    if (!albumInfo.is_paid) {
      briefItem = (
        <BriefItem albumInfo={albumInfo} />
      )
    }

    // 购买须知
    let buyNotetext = ''
    if (albumInfo.is_paid && albumInfo.price_type_id === 2 && !albumInfo.is_authorized) {
      buyNotetext = (
        <BuyNotes albumInfo={albumInfo} />
      )
    }

    // 其他说明
    let otherNotestext = ''
    if (albumInfo.is_paid && albumInfo.price_type_id === 2 && albumInfo.speaker_content && !albumInfo.is_authorized) {
      otherNotestext = (
        <OtherNotes albumInfo={albumInfo} />
      )
    }

    // 底部按钮 付费 (暂时只支持 整张专辑购买的情况)
    let pricehtml = ''
    if (albumInfo.is_paid && albumInfo.price_type_id === 2 && !albumInfo.is_authorized) {
      pricehtml = (
        <AlbumPrice albumInfo={albumInfo} {...this.props} onClick={() => this.goPay(albumInfo)} />
      )
    }
    if (!albumInfo) return null
    return (
      <div className='albuminfo-page'>

        {topalbum}

        {speaker}

        {briefItem}

        {buyNotetext}

        {otherNotestext}

        {pricehtml}

      </div>
    )
  }
}
export default AlbumInfo
