import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import { Link } from 'react-router-dom'
import './top20list.less'

@inject('store')
@observer
class TopList extends Component {
  render () {
    const paylist = this.props.store.bought.list
    return (
      <div className='toplist-components'>
        <div className='top-title'>付费节目TOP50</div>
        <div className='swiper-container'>
          <ul>
            {
              paylist.filter(v => v.composed_price_type !== 1).map((item, index) => {
                return (
                  <li key={index} id={item.id}>
                    <Link to={`/album/${item.id}`}>
                      <img src={item.cover_url_middle} alt='bg' />
                      <span>{item.album_title}</span>
                    </Link>
                  </li>
                )
              })
            }

          </ul>
        </div>
      </div>
    )
  }
}

export default TopList
