import React, { Component } from 'react'
import { throttle } from 'lodash'
import './scrollBar.less'

// 上拉刷新，待优化

export default class componentName extends Component {
  isTop = true
  startX = 0
  startY = 0

  componentDidMount () {
    // this.wrap.addEventListener('touchmove', (e) => {
    //   this.handleTouchMove(e)
    // }, {
    //   passive: false //  禁止 passive 效果
    // })
  }

  handleScroll = throttle(() => {
    this.isTop = false
    const scrollTop = this.wrap.scrollTop
    const wrapHeight = this.wrap.clientHeight
    const childrenHeight = this.children.clientHeight
    scrollTop === 0 ? this.isTop = true : (() => {})()
    if (childrenHeight > wrapHeight && childrenHeight - wrapHeight - scrollTop < 10) {
      this.props.onReachBottom()
    }
  }, 500)

  // onScrollTop = throttle(() => {
  //   this.props.onReachTop()
  // }, 2000)

  // handleTouchMove = (e) => {
  //   const touch = e.touches[0]
  //   if ((touch.pageY - this.startY) > 200 && this.isTop) {
  //     e.preventDefault()
  //     this.onScrollTop()
  //   }
  // }

  // handleTouchStart = (e) => {
  //   const touch = e.touches[0]
  //   this.startX = touch.pageX
  //   this.startY = touch.pageY
  // }

  render () {
    const { style } = this.props
    return (
      <div
        style={style}
        ref={(wrap) => { this.wrap = wrap }}
        className='scrollBar'
        onScroll={this.handleScroll}
        // onTouchStart={this.handleTouchStart}
      >
        <div ref={(children) => { this.children = children }}>{this.props.children}</div>
      </div>
    )
  }
}
