import { observable, action, runInAction } from 'mobx'

import subscribeAlbum from '@/request/api/subscribeAlbum'
import subscribeOper from '@/request/api/subscribeOper'

class Subscribe {
  @observable subscribeList=[]
  @observable operType=''
  @observable albumId=''

  @action
  async subscribeOper (type, id) {
    await subscribeOper(type, id).then((data) => {
      runInAction(() => {
        this.subscribeAlbum()
      }
      )
    }).catch(() => {
      console.log('取消订阅失败')
    })
  }

  @action
  async subscribeAlbumAfter (timeline, count) {
    const info = await subscribeAlbum(timeline, count).then(res => res)
    runInAction('subscribeList', () => {
      this.subscribeList = info.subscribe_albums || []
    })
  }
  @action
  async subscribeAlbum () {
    const info = await subscribeAlbum(0, 10).then(res => res)
    runInAction('subscribeList', () => {
      // this.subscribeAlbumAfter(subscribe_albums.timeline, 20)  // 订阅专辑会被过滤掉 不做二次调用
      this.subscribeList = info.subscribe_albums || []
    })
  }
}
export default Subscribe
