import React, { Component } from 'react'
// import { Link } from 'react-router-dom'
import CardItemSmall from '@/components/cardList-small/cardSmall'
import {classifyText} from '@/constant'

import './classifCard.less'

class ClassifCard extends Component {
  constructor (props) {
    super(props)
    this.state = {
      current: null

    }
  }

  onClick=async (ele, index, e) => {
    e.stopPropagation()

    // 通过点击tag 获取list 播放第一首
    const { player } = this.props.store
    await this.props.store.category.getAlbumByTag(ele)
    let id = this.props.store.category.firstId
    this.props.store.player.play(id)

    if (player.track && player.track.id === +id && player.playState === 2) {
      this.props.store.player.playAndPause()
    } else {
      this.props.store.player.play(id)
    }
    this.setState({
      isCurrent: index
    })
  }

  onGetList=(name) => {
    // 点击进入列表页面
    this.props.history.push(`/classify/${name}`)
  }
  render () {
    return (
      <div className='classifylist-card-container'>
        <ul>
          {
            classifyText.classifyListTop.map((ele, index) => {
              return (
                <li key={index} onClick={() => this.onGetList(ele.tagname)}>
                  <CardItemSmall
                    title={ele.title}
                    bgUrl={ele.bgUrl}
                    isShowPlay={ele.isShowPlay}
                    tagName={ele.tagname}
                    onClick={(e) => this.onClick(ele.tagname, index, e)}
                    current={this.state.isCurrent === index}
                    playState={this.props.store.player.playState}
                  />
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default ClassifCard
