import React, { Component } from 'react'
import classnames from 'classnames'
import { observer, inject } from 'mobx-react'
import './index.less'

@inject('store')
@observer
class TabNav extends Component {
  constructor (props) {
    super(props)
    this.state = {
      current: 0
    }
  }

  itemNav = (index) => {
    return index === this.state.current ? 'item-title active' : 'item-title'
  }

  itemCon = (index) => {
    return index === this.state.current ? 'con active' : 'con'
  }
  onClick = (index, keys) => {
    this.setState({
      current: index
    })
    if (this.props.request) {
      this.props.store.category.getPayRankAlbum(keys)
    }
  }

  render () {
    let borderClass = classnames(this.props.isborder ? ' itemContainer border' : 'itemContainer')
    return (
      <div className='tabNav-container' >
        <ul className={borderClass}>
          {
            React.Children.map(this.props.children, (element, index) => {
              return (
                <li key={element.props.keys} onClick={() => this.onClick(index, element.props.keys)} className={this.itemNav(index)}>{ element.props.name }</li>
              )
            })
          }
        </ul>

        <div className='itemContent'>
          {
            React.Children.map(this.props.children, (element, index) => {
              return (
                <div key={index} onClick={() => { this.setState({ current: index }) }} className={this.itemCon(index)}>{ element }</div>
              )
            })
          }
        </div>
      </div>

    )
  }
}

export default TabNav
