import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import ListItems from '@/components/itemList/list'

@inject('store')
@observer
class ResultList extends Component {
  render () {
    let htmls = ''
    if (this.props.show === 'albums') {
      htmls = (
        this.props.store.search.albums.list.filter(v => v.composed_price_type !== 1).map((item, index) => {
          return (
            <div key={index} type={item.composed_price_type}>
              <ListItems
                albumUrl={`/album/${item.id}`}
                title={item.album_title}
                info={item.album_intro}
                playcount={item.play_count}
                setNum={item.include_track_count}
                imgUrl={item.cover_url_middle} />
            </div>

          )
        })
      )
    }

    if (this.props.show === 'tracks') {
      htmls = (
        this.props.store.search.tracks.list.map((item, index) => {
          return (
            <div key={index}>
              <ListItems
                albumUrl={`/player/${item.id}`}
                title={item.track_title}
                info={item.subordinated_album.album_title}
                announcer={item.announcer.nickname}
                time={item.duration}
                playcount={item.play_count}
                imgUrl={item.cover_url_middle} />
            </div>
          )
        })
      )
    }

    if (this.props.show === 'radios') {
      htmls = (
        this.props.store.search.radios.list.map((item, index) => {
          return (
            <div key={index}>
              <ListItems
                title={item.radio_name}
                albumUrl={`/player/${item.id}`}
                playcount={item.radio_play_count}
                imgUrl={item.cover_url_small} />
            </div>
          )
        })
      )
    }

    return (
      <div className='resultlist-container'>
        {htmls}
      </div>
    )
  }
}
export default ResultList
