import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import ListAlbum from '@/components/itemList/list'
import TopSwiperList from './components/top20list'
import './index.less'

@inject('store')
@observer
class BoughtList extends Component {
  componentDidMount () {
    this.props.store.bought.payAllList()
    this.props.store.bought.boughtList()
  }
  render () {
    const {boughdata} = this.props.store.bought
    return (
      <div className='boughtlist-page'>
        <TopSwiperList paylist={this.props.store.bought.payall} />

        <div className='mylist'>
          <div className='title'>我的已购</div>
          {
            !boughdata.lenght
              ? <div>
                {
                  boughdata.map((item, index) => {
                    return (
                      <div key={index}>
                        <ListAlbum
                          albumUrl={`/album/${item.id}`}
                          title={item.album_title}
                          imgUrl={item.cover_url_middle}
                          playcount={item.play_count}
                          setNum={item.include_track_count}
                          info={item.last_uptrack.track_title}
                        />
                      </div>
                    )
                  })
                }
              </div>
              : <div className='null'>
                <img src={require('../../assets/images/Images/img_null.png')} alt='bg' />
                <p>没有购买记录</p>
              </div>
          }

        </div>

      </div>
    )
  }
}

export default BoughtList
