import { observable, action, runInAction, computed } from 'mobx'
// import getAlbumsInfo from '@/request/api/getBatch'
import { getDeviceBatchAlbum } from '@/request/api/getBatch'
import getTracks from '@/request/api/albumsBrowse'
import isSubscribed from '@/request/api/isSubscribed'
import subscribeOper from '@/request/api/subscribeOper'

class Album {
  @observable albumInfo = {}
  @observable tracks = { tracks: [] }
  @observable tracksList=[]
  @observable currentPage = 1
  @observable type = 'list'
  @observable isAsc = true
  @observable isShowFilter = false
  @observable isLoading = true
  @observable issubscribed=null

  count = 20

  @computed get sort () {
    const sort = this.isAsc ? 'asc' : 'desc'
    return sort
  }

  @computed get filters () {
    const { include_track_count } = this.albumInfo

    if (!include_track_count) { return [] }

    const maxIndex = include_track_count % this.count
    const page = Math.ceil(include_track_count / this.count)
    let filters = []

    for (let index = 1; index < page; index += 1) {
      const min = (index - 1) * 20 + 1
      const max = index * 20
      const filter = this.isAsc ? [min, max] : [max, min]
      filters.push(filter)
    }
    const lastMin = (page - 1) * 20 + 1
    const lastMax = (page - 1) * 20 + maxIndex

    const last = this.isAsc ? [lastMin, lastMax] : [lastMax, lastMin]
    filters.push(last)

    filters = this.isAsc ? filters : filters.reverse()
    return filters
  }

  @action.bound
  setType (type) {
    this.type = type
  }

  @action.bound
  switchSort () {
    this.isAsc = !this.isAsc
    const { id } = this.albumInfo
    this.getTracks(id, this.currentPage)
  }

  @action.bound
  showFilters () {
    this.isShowFilter = !this.isShowFilter
  }

  @action.bound
  init (album_id) {
    // this.isSubscribed(album_id)
    if (+album_id === +this.albumInfo.id) {
      return
    }
    runInAction(() => {
      this.setType('list')
      this.albumInfo = {}
    })
    this.getAlbumInfo(album_id)
    this.getTracks(album_id, 1)
  }

  @action
  async getAlbumInfo (album_id) {
    const data = await getDeviceBatchAlbum(album_id).then(res => res)
    runInAction('album_id', () => {
      this.albumInfo = data
      this.issubscribed = data.is_subscribe
    })
  }

  @action
  async subscribeOper (type, id) {
    await subscribeOper(type, id).then((data) => {
      runInAction(() => {
        this.issubscribed = !this.issubscribed
      })
    }).catch(() => {
      console.log('网络错误')
    })
  }

  @action
  onSubscribe=() => {
    if (this.issubscribed) {
      this.subscribeOper(0, this.albumInfo.id)
    } else {
      this.subscribeOper(1, this.albumInfo.id)
    }
  }

  @action.bound
  async isSubscribed (album_id) {
    const res = await isSubscribed(album_id)
    runInAction('isSubscribed', () => {
      if (album_id.split(',').length > 1) {
        return null
      }
      this.issubscribed = res[album_id]
    })
  }
  @action.bound
  async getTracks (album_id, page) {
    runInAction('changePage', () => {
      this.currentPage = page
      this.tracks = { tracks: [] }
      this.isLoading = true
    })

    const sort = this.sort
    await getTracks({ album_id, page, sort }).then((res) => {
      runInAction('getTrackList', () => {
        this.tracks = res
        this.tracksList = res.paid_tracks
        this.currentPage = res.current_page
        this.isShowFilter = false
        this.isLoading = false
        document.querySelector('.albumList-items').scroll(0, 0)
      })
    }).catch(() => {
      runInAction('getTrackList', () => {
        this.isLoading = false
      })
    })
  }
}
export default Album
