
import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import Util from 'util'
import TabNav from '@/components/tabnav/tabNav'
import ListItems from '@/components/itemList/list'
import { Link } from 'react-router-dom'
import './index.less'

@inject('store')
@observer
export default class Classify extends Component {
  componentWillMount () {
    let name = this.props.match.params.name
    if (name !== '付费排行') {
      this.props.store.category.getAlbumByTag(name)
    }
    if (name === '付费排行') {
      this.props.store.category.getPayRank()
    }
  }
  componentDidMount () {

  }

  render () {
    if (!this.props.store.category.albumlistBytag) return null
    const albumlistByTagInfo = this.props.store.category.albumlistBytag
    const paidalbums = this.props.store.category.paidalbums // 通过tag 获得的付费专辑
    const payRanksList = this.props.store.category.payRanksList // 排行榜单
    // const {paid_albums} = this.props.store.category.payRanksAlbumList // 排行榜单下的付费专辑列表

    let htmls = ''
    if (this.props.match.params.name === '付费排行') {
      if (!payRanksList) return null
      htmls = (
        <TabNav isborder request>
          {
            payRanksList.map((item, index) => {
              // if (!paid_albums) return null
              return (
                <div className='item' key={index} name={item.rank_title} keys={item.rank_key} type={item.composed_price_type}>
                  <ul>
                    {
                      this.props.store.category.payRanksAlbumList.filter(v => v.composed_price_type !== 1).map((item, index2) => {
                        return (
                          <li key={index2}>
                            <Link to={`/album/${item.id}`}>
                              <div className='ordernum'>{index2 + 1}</div>
                              <div className='right-info'>
                                <div className='img'><img src={item.cover_url_middle} alt='' /></div>
                                <div className='text-info'>
                                  <p className='name'>{item.album_title}</p>
                                  {item.album_intro &&
                                  <p className='subtitle'>{item.album_intro}</p>
                                  }
                                  <p className='count'>
                                    <span className='playcount'>
                                      <img src={require('../../assets/images/Icons/info_ic_playtimes.png')} alt='' />
                                      {Util.transPlayCount(item.play_count)}
                                    </span>
                                    <span className='setnum'>
                                      <img src={require('../../assets/images/Icons/info_ic_track.png')} alt='' />
                                      {Util.transPlayCount(item.include_track_count)}集
                                    </span>
                                    <span className='price'>
                                      {JSON.parse(item.price_type_detail)[0].discounted_price}
                                      {JSON.parse(item.price_type_detail)[0].price_unit}
                                    </span>
                                  </p>
                                </div>
                              </div>
                            </Link>
                          </li>
                        )
                      })
                    }
                  </ul>
                </div>
              )
            })
          }
        </TabNav>
      )
    }
    if (this.props.match.params.name !== '付费排行') {
      htmls = (
        <ul id={albumlistByTagInfo.category_id}>
          {
            paidalbums.filter(v => v.composed_price_type !== 1).map((item, index) => {
              return (
                <div key={index} type={item.composed_price_type}>
                  <ListItems
                    albumUrl={`/album/${item.id}`}
                    title={item.album_title}
                    info={item.album_intro}
                    playcount={item.play_count}
                    setNum={item.include_track_count}
                    imgUrl={item.cover_url_middle} />
                </div>

              )
            })
          }

        </ul>
      )
    }

    return (
      <div className='classifylist-page'>

        {htmls}

      </div>
    )
  }
}
