import { observable, action, runInAction } from 'mobx'

import boughtList from '@/request/api/boughtAlbum'
import payAllList from '@/request/api/payList'
import boughtStatus from '@/request/api/boughtStatus'

class Bought {
  @observable boughdata=[]
  @observable payall=[]
  @observable list=[]
  @observable isboughted=null

  @action.bound
  async getboughtStatus (ids) {
    const res = await boughtStatus(ids)
    const {has_bought} = res[0]
    runInAction('boughtStatus', () => {
      this.isboughted = has_bought
    })
  }

  @action.bound
  async boughtList () {
    const res = await boughtList().then(data => data)
    runInAction('boughtList', () => {
      this.boughdata = res || []
    })
  }

  @action.bound
  async payAllList () {
    const res = await payAllList().then((data) => {
      const {paid_albums} = data
      runInAction('paylist', () => {
        this.payall = res || []
        this.list = [...paid_albums]
      })
    })
  }
}
export default Bought
