import { observable, action, runInAction } from 'mobx'
import { uniq } from 'lodash'
import searchAll from '@/request/api/searchAll'
import searchHotWords from '@/request/api/searchHotWords'
import searchSuggest from '@/request/api/searchSuggest'
import { getData, setData } from '../utils/index'

class Search {
  @observable keyWord = ''
  @observable type = 'albums'
  @observable isFetching = false
  @observable page = 0
  @observable albums = { total_count: 0, list: [] }
  @observable tracks = { total_count: 0, list: [] }
  @observable radios = { total_count: 0, list: [] }
  @observable hotWord = []
  @observable history = []
  @observable searchSuggestList=[]
  @observable keywordsList=[]
  @observable showSuggestList=false

  constructor () {
    this.history = getData('search_history') ? getData('search_history') : []
  }

  @action.bound async searchSuggest (q) {
    await searchSuggest(q).then((data) => {
      const {albums, keywords} = data
      runInAction(() => {
        this.searchSuggestList = albums
        this.keywordsList = keywords
        this.showSuggestList = true
      })
    }).catch(() => {
      this.showSuggestList = false
    })
  }
  @action.bound setKeyWord (keyWord) {
    this.keyWord = keyWord
    this.albums = { total_count: 0, list: [] }
    this.tracks = { total_count: 0, list: [] }
    this.radios = { total_count: 0, list: [] }
    this.page = 0
    this.setHistory(keyWord)
    this.fetchData()
  }

  @action.bound setType (type) {
    this.type = type
  }

  @action setHistory (text) {
    if (!text) { return }
    let history = getData('search_history') ? getData('search_history') : []
    history = uniq([text, ...history]).slice(0, 6)
    this.history = history
    setData('search_history', history)
  }

  @action.bound clearHistory () {
    setData('search_history', [])
    this.history = []
  }

  @action.bound async changeHotWords () {
    this.hotWord = []
    const hotWord = await searchHotWords().then(data => data)
    runInAction(() => {
      this.hotWord = hotWord
    })
  }

  @action.bound async getHotWords () {
    const hotWord = await searchHotWords().then(data => data)

    runInAction(() => {
      this.hotWord = hotWord
    })
  }

  @action.bound
  async fetchData () {
    if (this.keyWord === '' || this.isFetching) { return }
    // 当前类型数据加载完毕
    if (this[this.type].list.length && this[this.type].current_page === this[this.type].total_page) {
      return
    }

    this.isFetching = true

    await searchAll(this.keyWord, this.page + 1).then((data) => {
      const { album_list, radio_list, track_list } = data
      runInAction(() => {
        this.albums = {
          ...this.albums,
          ...album_list,
          list: [...this.albums.list, ...album_list.albums]
        }

        this.radios = {
          ...this.radios,
          ...radio_list,
          list: [...this.radios.list, ...radio_list.radios]
        }
        this.tracks = {
          ...this.tracks,
          ...track_list,
          list: [...this.tracks.list, ...track_list.tracks]
        }

        this.page = this.page + 1
        this.isFetching = false
      })
    }).catch(() => {
      runInAction(() => {
        this.isFetching = false
      })
    })
  }
}

export default Search
