import { observable, action, runInAction, computed } from 'mobx'
import { getDeviceBatchAlbum } from '@/request/api/getBatch'
import initXmPlayer from '../utils/xmPlayer'
import { getFetchingLock } from '@/utils'
import subscribeOper from '@/request/api/subscribeOper'
import Toast from '@/components/toast'
import trackLast from '../request/api/trackLast'
import trackSeq from '../request/api/trackSeq'

class Player {
  @observable track = {}
  @observable album = {}
  @observable trackList = []
  @observable position = 0
  @observable playState = -1
  @observable isAsc = true
  @observable playMode = 0
  @observable isShowTrackList = false
  @observable isError = false
  @observable issubscribed = null
  @observable istrackFree=null
  @observable isShowpayLink=true

  // normal instance var
  currentPos = 0
  isRecordsDesc = false
  isFetching = false
  errorTrackId = null
  fetchingLock = getFetchingLock(10000)

  constructor () {
    initXmPlayer()
  }

  @computed
  get percentage () {
    if (!this.track.duration) { return 0 }
    const percentage = this.position / this.track.duration
    return `${percentage.toFixed(4) * 100}%`
  }

  @computed
  get trackIndex () {
    const { track, trackList } = this
    const index = trackList.findIndex(item => +track.id === +item.id)
    return index
  }
  @computed
  get isTrackInTop () {
    const result = this.trackIndex === 0
    return result
  }
  @computed
  get isTrackInBottom () {
    const result = this.trackIndex === (this.trackList.length - 1)
    return result
  }

  getSort = (reverse = false) => {
    const firstOrder = !this.isRecordsDesc ? this.isAsc : !this.isAsc
    const secondOrder = reverse ? !firstOrder : firstOrder
    return secondOrder ? 'asc' : 'desc'
  }

  @action
  handleUnusable = async (sound) => {
    this.errorTrackId = sound.id
    await this.updateTracks(sound)
    if (this.album.is_paid) {
      Toast('付费节目购买专辑后可畅听', '', 1000)
    } else {
      Toast('音频不可用')
    }
  }

  @action
  updateTracks = async (sound) => {
    const { album_id, id, is_paid, is_free } = sound
    let track = {}
    // 声音是否是免费
    if (is_paid && is_free) {
      this.istrackFree = true
    }

    // 更新专辑详情
    await this.refreshAlbum(sound.album_id)

    // 播放历史播放位置
    if (!this.currentPos === 0) {
      this.seek(this.currentPos)
      this.clearCurrentPos()
    }

    // 判断是否存在于当前列表，是赋予新的track，否重新拉取列表
    track = this.trackList.find(item => item.id === +id)
    if (!track) {
      const sort = this.getSort()
      const { items: tracks } = await trackLast({ id, album_id, sort })
      track = tracks.find(item => item.id === +id)
      this.setTrackList(tracks)
    }
    this.setTrack(track || {})
  }

  play = async (id = '', currentPos = 0) => {
    // 一些播放音频无法触发回调bug
    const track = this.trackList.find(item => item.id === +id)
    if (track) this.setTrack(track)

    window.xm.xmPlayer.play(id)
    this.setCurrentPos(currentPos)
  }

  setCurrentPos = (currentPos) => {
    this.currentPos = currentPos
  }

  clearCurrentPos = () => {
    this.currentPos = 0
  }

  @action.bound
  playAndPause = () => {
    switch (this.playState) {
      case 2:
        window.xm.xmPlayer.pause()
        break
      case 3:
        window.xm.xmPlayer.play()
        break
      default:
        window.xm.xmPlayer.audio.sound = {}
        window.xm.xmPlayer.play(this.track.id)
        break
    }
  }

  prev = () => {
    if (this.isTrackInTop) {
      this.getPrev().then(() => window.xm.xmPlayer.prev())
    } else {
      if (this.errorTrackId) {
        const playList = this.getPlayList()
        const prevId = playList[playList.indexOf(+this.errorTrackId) - 1]
        this.errorTrackId = null
        this.play(prevId)
      } else {
        window.xm.xmPlayer.prev()
      }
    }
  }

  next = () => {
    if (this.isTrackInBottom) {
      this.getNext().then(() => window.xm.xmPlayer.next())
    } else {
      if (this.errorTrackId) {
        const playList = this.getPlayList()
        const nextId = playList[playList.indexOf(+this.errorTrackId) + 1]
        this.errorTrackId = null
        this.play(nextId)
      } else {
        window.xm.xmPlayer.next()
      }
    }
  }

  @action
  getPrev = async () => {
    const sort = this.getSort(true)
    const { id, album: {id: album_id} } = this.track
    let tracks = []
    const result = await this.fetchingLock(() => {
      return trackSeq({ id, album_id, sort })
    })
    result ? tracks = result.items : (() => {})()
    const trackList = [...this.trackList]
    trackList.unshift(...tracks.reverse())
    this.setTrackList(trackList)
  }

  @action
  getNext = async () => {
    const sort = this.getSort()
    const { id, album: {id: album_id} } = this.track
    let tracks = []
    const result = await this.fetchingLock(() => {
      return trackSeq({ id, album_id, sort })
    })
    result ? tracks = result.items : (() => {})()
    const trackList = [...this.trackList]
    trackList.push(...tracks)
    this.setTrackList(trackList)
  }

  @action
  fetchPrev = async () => {
    const sort = this.getSort(true)
    const { id, album: {id: album_id} } = this.trackList[0]
    let tracks = []
    const result = await this.fetchingLock(() => {
      return trackSeq({ id, album_id, sort })
    })
    result ? tracks = result.items : (() => {})()
    const trackList = [...this.trackList]
    trackList.unshift(...tracks.reverse())
    this.setTrackList(trackList)
  }

  @action
  fetchNext = async () => {
    const sort = this.getSort()
    const { id, album: {id: album_id} } = this.trackList[this.trackList.length - 1]
    let tracks = []
    const result = await this.fetchingLock(() => {
      return trackSeq({ id, album_id, sort })
    })
    result ? tracks = result.items : (() => {})()
    const trackList = [...this.trackList]
    trackList.push(...tracks)
    this.setTrackList(trackList)
  }

  @action
  clearNextPage = () => {
    this.nextPage = 1
  }

  @action
  incNextPage = () => {
    this.nextPage++
  }

  seek = (position) => {
    if (this.playState !== 4) window.xm.xmPlayer.seek(position)
  }

  getPlayList = () => {
    const playList = window.xm.xmPlayer.getPlaylist()
    return playList
  }

  @action
  setPlayState = (playState) => {
    runInAction('playState', () => {
      this.playState = playState
    })
  }

  @action
  setPosition = (position) => {
    runInAction('setPosition', () => {
      this.position = position
    })
  }

  @action
  setTrack = (track) => {
    runInAction('setTrack', () => {
      this.track = track
    })
  }

  @action
  setTrackList = (trackList) => {
    runInAction('trackList', () => {
      this.trackList = trackList
    })
    // playlist逻辑统一放在tracklist内部， 保证两者统一
    const list = []
    trackList.map(item => list.push(item.id))
    window.xm.xmPlayer.setPlaylist(list)
  }

  @action
  setPlayMode = (playMode) => {
    runInAction('setPlayMode', () => {
      this.playMode = playMode
    })
  }

  // 切换模式

  @action
  switchPlayMode = () => {
    const playMode = (this.playMode + 1) % 3
    window.xm.xmPlayer.setPlayMode(playMode)
  }

  // 排序
  @action
  switchSort = () => {
    runInAction('switchSort', () => {
      this.isAsc = !this.isAsc
    })
    const trackList = [...this.trackList].reverse()
    this.setTrackList(trackList)
  }

  @action
  showTrackList = () => {
    runInAction('showTrackList', () => {
      this.isShowTrackList = !this.isShowTrackList
    })
  }

  @action
  async subscribeOper (type, id) {
    await subscribeOper(type, id).then((data) => {
      runInAction(() => {
        this.issubscribed = !this.issubscribed
      })
    }).catch(() => {
      console.log('网络错误')
    })
  }

  @action
  onSubscribe=() => {
    if (this.issubscribed) {
      this.subscribeOper(0, this.album.id)
    } else {
      this.subscribeOper(1, this.album.id)
    }
  }

  @action
  refreshAlbum = async (album_id) => {
    // 专辑不同则初始化播放顺序,和专辑详情
    if (album_id !== this.album.id) {
      const album = await getDeviceBatchAlbum(album_id)
      runInAction(() => {
        this.album = album
        this.isAsc = true
        this.isRecordsDesc = this.album.is_records_desc
        this.issubscribed = this.album.is_subscribe
      })
    }
  }

  @action
  setError = (isError) => {
    runInAction('setError', () => {
      this.isError = isError
    })
  }
}
export default Player
