import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
// import TabLink from '@/components/tabLink/tabLink'
import ListAlbum from '@/components/itemList/list'
import './index.less'

@inject('store')
@observer
class Subscribe extends Component {
  componentDidMount () {
    this.props.store.subscribe.subscribeAlbum()
  }

  onTouch = (type, id) => {
    this.props.store.subscribe.subscribeOper(type, id)
  }

  render () {
    // const {subscribe_albums} = this.props.store.subscribe.subscribeList

    return (
      <div className='subscribe-page'>
        {/* <TabLink>
          <div link='/subscribeAlbum'>订阅的专辑</div>
          <div link='/subscribeRadio'>订阅的广播</div>
        </TabLink> */}

        {
          this.props.store.subscribe.subscribeList.length
            ? <div>
              {
                this.props.store.subscribe.subscribeList.map((item, index) => {
                  return (
                    <div key={index}>
                      <ListAlbum
                        albumUrl={`/album/${item.id}`}
                        title={item.album_title}
                        imgUrl={item.cover_url_middle}
                        info={item.last_uptrack.track_title}
                        date={item.updated_at}
                        albumAnnouncer={item.announcer.nickname}
                        onTouch={() => this.onTouch(0, item.id)}
                        isStart />
                    </div>
                  )
                })
              }
            </div>
            : <div className='null'>
              <img src={require('../../assets/images/Images/img_null.png')} alt='bg' />
              <p>暂时无法查看了</p>
            </div>

        }

      </div>
    )
  }
}

export default Subscribe
