import React from 'react'
import { observer, inject } from 'mobx-react'
// import LoadingBar from '@/components/Loading'
import './startSearch.less'

function Start ({ store, searchWord }) {
  const { hotWord, history, clearHistory, getHotWords } = store.search

  if (!hotWord.length) {
    getHotWords()
  }
  return (
    <div className='searchStart-container'>
      <div className='searchStart-title'>
        <div className='searchStart-title--txt'>热门搜索</div>
        {/* <div onClick={changeHotWords} className='searchStart-title--action'>
          换一批
        </div> */}
      </div>
      <div className='searchStart-hot'>
        {hotWord.length
          ? hotWord.map(item =>
            <span className='result-item' onClick={() => searchWord(item.search_word)} key={item.search_word}>{item.search_word}</span>
          )
          : '拼命加载中。。。'
        }
      </div>
      {
        history.length > 0
          ? <div>
            <div className='searchStart-title'>
              <div className='searchStart-title--txt'>历史搜索</div>
              <div className='searchStart-title--action' onClick={clearHistory}>
                清除历史
              </div>
            </div>
            <div className='searchStart-history'>
              <ul>
                {history.map(item =>
                  <li className='history-item' onClick={() => searchWord(item)} key={item}>{item}</li>
                )}

              </ul>

            </div>
          </div>
          : null
      }
    </div>
  )
}

export default inject('store')(observer(Start))
