import axios from 'axios'
import forge from 'node-forge'
import { APP_KEY, APP_SECRET, REDIRECT_URL, DEVICE_ID, BASE_URL, PAY_URL, DEVICE_URL, AUTH_URL, M_SITE_URL, PAY_OLD_URL } from '@/constant'
import { getData, setData, showError, showSuccess, generateRandom } from '@/utils'
import store from '@/store'

// 计算sig
const calcuSig = (params, key) => {
  const arr = []
  const hmac = forge.hmac.create()
  const md = forge.md.md5.create()
  Object.entries(params).sort().map(item => arr.push(`${item[0]}=${item[1]}`))
  const sEncoded = forge.util.encode64(forge.util.encodeUtf8(arr.join('&')))
  hmac.start('sha1', key)
  hmac.update(sEncoded)
  md.update(hmac.digest().data)
  const sig = md.digest().toHex()
  return sig
}

// 进入登录页面
export const OAuth2Login = () => {
  const baseURL = `${M_SITE_URL}/login?fromUri=`
  const leftURL = `${AUTH_URL}/v2/authorize?
    client_id=${APP_KEY}&
    response_type=code&
    redirect_uri=${REDIRECT_URL}&
    device_id=${DEVICE_ID}&
    client_os_type=3`
  const URL = baseURL + encodeURIComponent(leftURL.replace(/[\r\n ]/g, ''))
  location.href = URL
}

// 进入下单页面
export const SubmitOrderPage = async (price_type, pay_content, price, return_url) => {
  const access_token = await getToken()
  // 公共参数
  const params = {
    app_key: APP_KEY,
    device_id: DEVICE_ID,
    client_os_type: 1,
    access_token: access_token,
    nonce: generateRandom(),
    timestamp: +new Date(),
    price_type: price_type,
    pay_content: pay_content,
    price: price,
    return_url: return_url
  }

  const sig = calcuSig({ ...params }, APP_SECRET)

  const baseURL = `${DEVICE_URL}/openapi-smart-device-pay-api/pay/goto_prepare_order`
  const leftURL = `?app_key=${params.app_key}&
  device_id=${params.device_id}&
  client_os_type=1&
  access_token=${params.access_token}&
  nonce=${params.nonce}&
  timestamp=${params.timestamp}&
  price_type=${params.price_type}&
  pay_content=${params.pay_content}&
  price=${params.price}&
  return_url=${params.return_url}&sig=${sig}`
  window.location.href = baseURL + leftURL.replace(/[\r\n ]/g, '')
}

// 读取token
const getToken = async ({enforceRefresh = false} = {}) => {
  let token = getData('token')
  // 本地存在
  if (token) {
    const { access_token, lastTime, expires_in, refresh_token } = token
    // 本地判断未过期
    if ((+new Date() - lastTime) < expires_in && !enforceRefresh) {
      if (store.user.accessToken !== access_token) store.user.setAccessToken(access_token)
      return access_token
    } else {
      const access_token = await refreshToken(refresh_token)
      if (access_token) return access_token
      OAuth2Login()
    }
  } else {
    OAuth2Login()
  }
}

// 刷新access_token
const refreshToken = async (rt) => {
  try {
    const data = await axios({
      url: `${AUTH_URL}/refresh_token`,
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      timeout: 10000,
      params: {
        grant_type: 'refresh_token',
        redirect_uri: REDIRECT_URL,
        client_id: APP_KEY,
        client_secret: APP_SECRET,
        device_id: DEVICE_ID,
        refresh_token: rt
      },
      data: {}
    })
    const {access_token, refresh_token, expires_in} = data.data
    store.user.setAccessToken(access_token)
    setData('token', {access_token, refresh_token, expires_in: expires_in * 1000, lastTime: +new Date()})
    return access_token
  } catch (e) {
    return false
  }
}

// 登出界面，清楚localstorage和store
export const logOut = () => {
  localStorage.removeItem('token')
  store.user.clearAccessToken()
  location.href = '/'
}

// 配置拦截器的自定义axios
const customAxios = axios.create({
  timeout: 10000,
  headers: {
    'Content-Type': 'application/x-www-form-urlencode'
  },
  responseType: 'json'
})

customAxios.interceptors.response.use(response => response.data, (err) => {
  if (err && err.response && err.response.data.error_code === 'ximalaya.oauth2.access_token_invalid_or_expired') {
    // 出现acess_token过期则重新获取token
    getToken({enforceRefresh: true})
    showSuccess('为您重新获取用户认证，请稍后再试')
    return 'try again'
  }
  return Promise.reject(err)
})

export const request = (method, baseURL = BASE_URL) => async (url, params = {}) => {
  const access_token = await getToken()
  // 公共参数
  const publicParams = {
    app_key: APP_KEY,
    device_id: DEVICE_ID,
    client_os_type: 3,
    access_token: access_token,
    nonce: generateRandom(),
    timestamp: +new Date()
  }
  const sig = calcuSig({ ...publicParams, ...params }, APP_SECRET)

  return customAxios({
    url,
    method,
    baseURL,
    params: {
      ...publicParams,
      ...params,
      sig
    },
    data: {}
  }).catch((err) => {
    showError('network error')
    return Promise.reject(err)
  })
}

export const get = request('GET')
export const post = request('POST')
export const getPay = request('GET', PAY_URL)
export const postPay = request('POST', PAY_URL)
export const getDevice = request('GET', DEVICE_URL)
export const postDevice = request('POST', DEVICE_URL)
export const getOrderPay = request('GET', PAY_OLD_URL)
export const postOrderPay = request('POST', PAY_OLD_URL)
