import { observable, action, runInAction } from 'mobx'

import getTags from '@/request/api/getTags'
import getAlbumByTag from '@/request/api/getAlbumByTag'
import getPayRank from '@/request/api/getPayRank'
import getPayRankAlbum from '@/request/api/getPayRankAlbum'

class Category {
  @observable taglist=[]
  @observable albumlistBytag={};
  @observable paidalbums=[]
  @observable payRanksList=[]
  @observable payRanksAlbumList=[]
  @observable keyname=null
  @observable firstId=null // 其他分类的第一个免费的声音
  @observable firstIdRank= null // 付费排行榜的 第一个免费的声音

  @action.bound
  async getTags () {
    const res = await getTags().then(data => data)
    runInAction('getTag', () => {
      this.taglist = res || []
    })
  }

  // 通过tag 获取list
  @action.bound
  async getAlbumByTag (name, page) {
    const res = await getAlbumByTag(name, page)
    const {paid_albums} = res
    runInAction('getAlbumByTag', () => {
      this.albumlistBytag = res
      this.paidalbums = paid_albums || []
      this.firstId = paid_albums[0] ? paid_albums[0].free_track_ids.split(',')[0] : ''
    })
  }

  // 获取付费排行专辑下的付费列表
  @action
  getPayRankAlbum =async (key) => {
    const res = await getPayRankAlbum(key)
    const {paid_albums} = res
    runInAction('getPayRankAlbum', () => {
      this.payRanksAlbumList = paid_albums || []
      this.firstIdRank = paid_albums[0] ? paid_albums[0].free_track_ids.split(',')[0] : ''
    })
  }

  // 获取付费排行榜
  @action
  async getPayRank () {
    const res = await getPayRank()
    runInAction('getPayrank', () => {
      this.payRanksList = res || []
      this.keyname = this.payRanksList[0].rank_key
    })
    await this.getPayRankAlbum(this.keyname)
  }
}
export default Category
